}

.btn-back {
  margin-top: 2rem;
  position: relative;
}
.btn-back .btn-link-gray {
  color: rgba(193, 193, 193, 0.5882352941);
  font-size: 1.5rem;
  bottom: -2rem;
  z-index: 33;
}
.btn-back .btn-link-gray:hover {
  color: #FFF !important;
}

.btn-primary-hover:hover {
  transition: 0.3s ease-in;
