/* ****** DROP ****** */
.drop-zone {
  height: 20rem;
  display: flex;
  align-items: center;
  justify-content: center;
  background: rgba(59, 97, 255, 0.05);
  background-image: url("data:image/svg+xml,%3csvg width='100%25' height='100%25' xmlns='http://www.w3.org/2000/svg'%3e%3crect width='100%25' height='100%25' fill='none' rx='20' ry='20' stroke='%2396B0CFFF' stroke-width='2' stroke-dasharray='11%2c 17' stroke-dashoffset='12' stroke-linecap='square'/%3e%3c/svg%3e");
  border-radius: 1.25rem;
  overflow: hidden;
  cursor: pointer;
  padding: 0;
  transition: all 0.3s;
}
.drop-zone-prompt {
  color: #635C7F;
  font-weight: 700;
  min-width: 12rem;
}
.drop-zone-prompt-img {
  display: block;
  margin: 0 auto;
  width: 5.3rem;
  height: 3.5rem;
  background-size: contain;
  background-repeat: no-repeat;
  margin-bottom: 1rem;
  background-image: url("/images/icons/add-file.svg");
}
.drop-zone-prompt-title {
  display: block;
  text-align: center;
  font-weight: 700;
  font-size: 1rem;
  margin-bottom: 1rem;
}
.drop-zone-prompt-subtitle {
  display: block;
  text-align: center;
  font-weight: 400;
  font-size: 1rem;
  margin-bottom: 1rem;
  color: #96B0CF;
  position: relative;
}
.drop-zone-prompt-subtitle::before, .drop-zone-prompt-subtitle::after {
  content: "";
  width: 35%;
  height: 1px;
  background-color: #96B0CF;
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
}
.drop-zone-prompt-subtitle::before {
  left: 0;
}
.drop-zone-prompt-subtitle::after {
  right: 0;
}
.drop-zone-input {
  display: none;
}
.drop-zone-over {
  border-style: solid;
  opacity: 0.7;
}
.drop-zone-thumb {
  width: 100%;
  height: 100%;
  border-radius: 1.25rem;
  overflow: hidden;
  background-size: cover;
  background-position: center;
  position: relative;
}
.drop-zone-thumb.bg-not-cover {
  background-size: auto;
  background-repeat: no-repeat;
  background-position-y: 40%;
}

.drop-zone-container .upload-file-name-row:not(:last-child) {
  border-bottom: 2px dashed #E3E3E3;
}
.drop-zone-container .upload-file-name {
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
  width: 11rem;
}

